import React from "react";
import { Button, Row, Col } from "reactstrap";

import Nouislider from "nouislider-react";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import Preference from "../../libs/Preference.js";
import guideMasterList from "../../assets/compendium/guideMasterList.json";

export default function Guidecontent(props) {
  const [guideid, setGuideid] = React.useState(null);
  const [search, setSearch] = React.useState(null);
  const [guideContent, setGuideContent] = React.useState(null);
  const [guideItem, setGuideItem] = React.useState(null);
  const [textZoom, setTextZoom] = React.useState(null);
  const [fontFamily, setFontFamily] = React.useState(null);

  const searchObj = React.useRef();

  const setRenderParameters = React.useCallback((guideid, opts, search) => {
    try {
      if (search) {
        setSearch(search);
      }
      if (guideid) {
        setGuideItem(
          guideMasterList[guideMasterList.length - parseInt(guideid) - 1]
        );

        setGuideContent(
          require("../../assets/compendium/" + guideid + "/content.js").default(
            "../../assets/compendium/" + guideid + "/images/",
            {
              hrefToAnotherGuide: (guideid, opts) => {
                setRenderParameters(guideid, opts);
                window.history.replaceState(
                  null,
                  null,
                  window.location.href.split("?")[0] + "?id=" + guideid
                );
              },
            }
          )
        );
        setGuideid(guideid);
      }
    } catch (ex) {
      console.log("Unable to find ID. Error: " + ex);
    }

    if (opts) {
      if (opts.scrollToUp) {
        scrollToTop();
      }
    }
  }, []);

  const scrollToTop = () => {
    window.scroll({ top: 0, left: 0, behavior: "smooth" });
  };

  const zoomUpdated = (zoomData) => {
    const newZoomValue = parseInt(zoomData[0]);
    setTextZoom(newZoomValue);
    const pref = new Preference();
    pref.setTextZoom(newZoomValue);
  };

  const fontFamilyUpdated = (fontFamilyData) => {
    const newFontFamilyValue = parseInt(fontFamilyData);
    setFontFamily(newFontFamilyValue);
    const pref = new Preference();
    pref.setFontFamily(newFontFamilyValue);
  };

  const copyUrlToClipboard = () => {
    navigator.clipboard.writeText(window.location.href);
    toast.info("Copied to clipboard!");
  };

  const setDefaultPref = React.useCallback(() => {
    const pref = new Preference();
    setTextZoom(pref.getTextZoom());
    setFontFamily(pref.getFontFamily());
  }, []);

  const downloadAsPDF = async () => {
    const guidePdf = await getGuidePdf();
    window.open(guidePdf.default, "_blank");
  };

  const getGuidePdf = async () => {
    return await import("../../assets/compendium/" + guideid + "/guide.pdf");
  };

  const zoomToSeachText = React.useCallback(() => {
    if (search && searchObj) {
      if (searchObj.current.childNodes[0]) {
        const fetchedGuideContent = searchObj.current.childNodes[0].childNodes;
        for (let i = 0; i < fetchedGuideContent.length; i++) {
          if (fetchedGuideContent[i].textContent.toUpperCase() === search) {
            fetchedGuideContent[i].className = "scrollToHere";
            setTimeout(() => {
              const findPos = (obj) => {
                var curtop = 0;
                if (obj.offsetParent) {
                  do {
                    curtop += obj.offsetTop;
                  } while ((obj = obj.offsetParent));
                  if (curtop > 70) {
                    curtop -= 70;
                  }
                  return [curtop];
                }
              };

              window.scroll(
                0,
                findPos(document.getElementsByClassName("scrollToHere")[0])
              );
            }, 1000);
            break;
          }
        }
      }
    }
  }, [search]);

  React.useEffect(() => {
    setRenderParameters(props.guideid, null, props.search);
    setDefaultPref();
    zoomToSeachText();
  }, [props, setRenderParameters, setDefaultPref, zoomToSeachText]);

  return (
    <Row className="margin-0">
      <Col
        className="mt-5 mt-sm-0 margin-auto guide-content-body"
        sm="9"
        xs="12"
      >
        {!guideItem ? (
          ""
        ) : (
          <div className="guide-content-style">
            <Row>
              <Col className="text-align-right" sm="3" xs="9">
                <img
                  alt="..."
                  className="img-fluid rounded shadow-lg width-150"
                  src={
                    require("assets/compendium/" +
                      guideid +
                      "/cover_thumbnail.jpg").default
                  }
                />
              </Col>
              <Col className="text-align-left" sm="9" xs="12">
                <h3 className="d-block text-uppercase font-weight-bold mb-4 guide-title">
                  {guideItem.title}
                </h3>
                <p>{guideItem.description}</p>
                <small>
                  <i className="fa fa-pencil-alt"></i> {guideItem.author} <br />
                  <i className="fa fa-sync"></i>{" "}
                  {new Date(guideItem.lastupdated).toLocaleString()}
                  <br />
                  Revision {guideItem.version}
                </small>
                <br />
                <br />
                <Button
                  color="primary"
                  type="button"
                  onClick={() => {
                    downloadAsPDF();
                  }}
                >
                  Download as PDF
                </Button>

                <Button
                  color="success"
                  type="button"
                  onClick={() => {
                    copyUrlToClipboard();
                  }}
                >
                  Copy URL
                </Button>
                <ToastContainer
                  position="top-center"
                  theme="dark"
                  autoClose={3000}
                  hideProgressBar={false}
                  newestOnTop={false}
                  closeOnClick
                  rtl={false}
                  pauseOnFocusLoss={false}
                  draggable={false}
                  pauseOnHover={false}
                />
              </Col>
            </Row>
          </div>
        )}

        <hr className="content-divider" />

        <Row>
          <Col className="text-align-center" sm="6" xs="12">
            <h6>Text Zoom (%)</h6>
            <div className="slider-container">
              <Nouislider
                connect={[true, false]}
                step={10}
                start={[textZoom]}
                range={{
                  min: [50],
                  "0%": [50, 10],
                  "10%": [60, 10],
                  "20%": [70, 10],
                  "30%": [80, 10],
                  "40%": [90, 10],
                  "50%": [100, 25],
                  "60%": [110, 10],
                  "70%": [120, 10],
                  "80%": [130, 10],
                  "90%": [140, 10],
                  "100%": [150, 10],
                  max: [150],
                }}
                pips={{
                  mode: "range",
                  density: 2,
                }}
                onSlide={zoomUpdated}
              />
            </div>
          </Col>
          <Col className="text-align-center" sm="6" xs="12">
            <h6>Font Style</h6>

            <Button
              color={fontFamily === 0 ? "info" : "default"}
              type="button"
              size="sm"
              onClick={() => {
                fontFamilyUpdated(0);
              }}
            >
              Default
            </Button>
            <Button
              color={fontFamily === 1 ? "info" : "default"}
              type="button"
              size="sm"
              onClick={() => {
                fontFamilyUpdated(1);
              }}
            >
              Consolas
            </Button>
            <Button
              color={fontFamily === 2 ? "info" : "default"}
              type="button"
              size="sm"
              onClick={() => {
                fontFamilyUpdated(2);
              }}
            >
              Roboto
            </Button>
            <Button
              color={fontFamily === 3 ? "info" : "default"}
              type="button"
              size="sm"
              onClick={() => {
                fontFamilyUpdated(3);
              }}
            >
              Lora
            </Button>
          </Col>
        </Row>

        <hr className="content-divider" />

        <div
          className={
            "GuideContent zoom-" + textZoom + " font-family-" + fontFamily
          }
          ref={searchObj}
        >
          {!guideContent ? "" : guideContent}
        </div>

        <Button
          color="success"
          type="button"
          className="scrolltotop-button"
          onClick={scrollToTop}
        >
          <i className="fa fa-arrow-up"></i>
        </Button>
      </Col>
    </Row>
  );
}
