import React from "react";
import { Link } from "react-router-dom";

import { Col } from "reactstrap";

export default function Gridcell(props) {
  const guideItem = props.guideItem;
  const activeView = props.activeView;

  return activeView === 0 ? (
    <Col className="mt-5 mt-sm-0 text-align-center" sm="3" xs="6">
      <Link to={"/guide?id=" + guideItem.id}>
        <img
          alt="..."
          className="img-fluid rounded shadow-lg width-150"
          src={
            require("assets/compendium/" +
              guideItem.id +
              "/cover_thumbnail.jpg").default
          }
        />
        <div className="d-block text-uppercase font-weight-bold mb-4 guide-title gridcell-title">
          {guideItem.title}
        </div>
      </Link>
      <small className="d-block mb-4 gridcell-details-style">
        <i className="fa fa-pencil-alt"></i> {guideItem.author} <br />
        <i className="fa fa-sync"></i>{" "}
        {new Date(guideItem.lastupdated).toLocaleString()} <br />
        Revision {guideItem.version}
      </small>
    </Col>
  ) : (
    <Col
      className="mt-5 mt-sm-0 text-align-left gridcell-list-style"
      sm="12"
      xs="12"
    >
      <Link to={"/guide?id=" + guideItem.id}>
        <img
          alt="..."
          className="img-fluid rounded shadow-lg height-100 inline-block vertical-align-bottom"
          src={
            require("assets/compendium/" +
              guideItem.id +
              "/cover_thumbnail.jpg").default
          }
        />
      </Link>
      <div className=" margin-left-30 inline-block">
        <Link to={"/guide?id=" + guideItem.id}>
          <div className="d-block text-uppercase font-weight-bold mb-4 guide-title gridcell-title">
            {guideItem.title}
          </div>
        </Link>
        <small className="d-block mb-4 gridcell-details-style margin-0">
          <i className="fa fa-pencil-alt"></i> {guideItem.author} <br />
          <i className="fa fa-sync"></i>{" "}
          {new Date(guideItem.lastupdated).toLocaleString()} <br />
          Revision {guideItem.version}
        </small>
      </div>

      <div className="  ">
        <br />
        <p>{guideItem.description}</p>
      </div>
    </Col>
  );
}
