import React from "react";

import Gridcell from "./Gridcell.js";
import guideMasterList from "../../assets/compendium/guideMasterList.json";

import {
  Button,
  Container,
  Row,
  Col,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
} from "reactstrap";

export default function Guidegrids() {
  const sortRenderText = [
    "Newest Guides",
    "Most Recently Updated",
    "Guides Name (A - Z)",
    "Guides Name (Z - A)",
  ];

  const [activeView, setActiveView] = React.useState(0);
  const [sortValue, setSortValue] = React.useState(0);

  const sortGuideList = () => {
    console.log(sortValue);
    if (sortValue === 1) {
      let tempguideMasterList = guideMasterList.slice();
      tempguideMasterList.sort((a, b) => {
        return new Date(a.lastupdated) < new Date(b.lastupdated) ? 1 : -1;
      });
      return tempguideMasterList;
    }
    if (sortValue === 2) {
      let tempguideMasterList = guideMasterList.slice();
      tempguideMasterList.sort((a, b) => {
        return a.title > b.title ? 1 : -1;
      });
      return tempguideMasterList;
    }
    if (sortValue === 3) {
      let tempguideMasterList = guideMasterList.slice();
      tempguideMasterList.sort((a, b) => {
        return a.title < b.title ? 1 : -1;
      });
      return tempguideMasterList;
    }
    return guideMasterList;
  };

  return (
    <div className="section section-basic" id="basic-elements">
      <img
        alt="..."
        className="path"
        src={require("assets/img/path1.png").default}
      />
      <Container>
        <div className="space-50" />
        <div id="images">
          <Row>
            <Col className="mt-5 mt-sm-0 text-align-left" sm="6" xs="9">
              <UncontrolledDropdown>
                <DropdownToggle
                  aria-expanded={false}
                  aria-haspopup={true}
                  caret
                  color="default"
                  data-toggle="dropdown"
                  nav
                >
                  <h3 className="inline-block">{sortRenderText[sortValue]}</h3>
                </DropdownToggle>
                <DropdownMenu aria-labelledby="navbarDropdownMenuLink">
                  <DropdownItem
                    href="#pablo"
                    onClick={(e) => {
                      e.preventDefault();
                      setSortValue(0);
                    }}
                  >
                    {sortRenderText[0]}
                  </DropdownItem>
                  <DropdownItem
                    href="#pablo"
                    onClick={(e) => {
                      e.preventDefault();
                      setSortValue(1);
                    }}
                  >
                    {sortRenderText[1]}
                  </DropdownItem>
                  <DropdownItem
                    href="#pablo"
                    onClick={(e) => {
                      e.preventDefault();
                      setSortValue(2);
                    }}
                  >
                    {sortRenderText[2]}
                  </DropdownItem>
                  <DropdownItem
                    href="#pablo"
                    onClick={(e) => {
                      e.preventDefault();
                      setSortValue(3);
                    }}
                  >
                    {sortRenderText[3]}
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Col>
            <Col className="mt-5 mt-sm-0 text-align-right" sm="6" xs="3">
              <Button
                className="btn-link icon-button"
                color={activeView === 0 ? "info" : "default"}
                type="button"
                onClick={() => {
                  setActiveView(0);
                }}
              >
                <i className="fa fa-table"></i>
              </Button>
              <Button
                className="btn-link icon-button"
                color={activeView === 1 ? "info" : "default"}
                type="button"
                onClick={() => {
                  setActiveView(1);
                }}
              >
                <i className="fa fa-list"></i>
              </Button>
            </Col>
          </Row>
          <Row>
            {sortGuideList().map((guideItem, index) => (
              <Gridcell
                key={index}
                uuid={index}
                guideItem={guideItem}
                activeView={activeView}
              />
            ))}
          </Row>
        </div>
      </Container>
    </div>
  );
}
