const __LOCALSTORAGE_INDEX = "preference";
export default class Preference {
  lsPref;
  textZoom;
  fontFamily;

  constructor() {
    let lsPref = JSON.parse(localStorage.getItem(__LOCALSTORAGE_INDEX));
    if (!lsPref) {
      lsPref = { textZoom: 100, fontFamily: 0 };
    }

    this.lsPref = lsPref;
    this.textZoom = lsPref.textZoom;
    this.fontFamily = lsPref.fontFamily;
  }

  getTextZoom = () => {
    return this.textZoom;
  };
  setTextZoom = (textZoom) => {
    this.lsPref.textZoom = textZoom;
    localStorage.setItem(__LOCALSTORAGE_INDEX, JSON.stringify(this.lsPref));
    this.textZoom = textZoom;
  };
  getFontFamily = () => {
    return this.fontFamily;
  };
  setFontFamily = (fontFamily) => {
    this.lsPref.fontFamily = fontFamily;
    localStorage.setItem(__LOCALSTORAGE_INDEX, JSON.stringify(this.lsPref));
    this.fontFamily = fontFamily;
  };
}
