import React from "react";
import Guidecontent from "views/Guide/Guidecontent.js";

export default function Index() {
  const [guideid, setGuideid] = React.useState(null);
  const [search, setSearch] = React.useState(null);

  React.useEffect(() => {
    const urlParams = window.location.href.split("?");
    if (urlParams.length > 1) {
      const urlParamArr = urlParams[1].split("&");
      for (let i = 0; i < urlParamArr.length; i++) {
        const paramValue = urlParamArr[i].split("=");
        if (paramValue[0] === "id") {
          setGuideid(paramValue[1]);
        }
        if (paramValue[0] === "search") {
          setSearch(decodeURIComponent(paramValue[1]).toUpperCase());
        }
      }
    }
  }, []);

  return (
    <>
      <div className="wrapper">
        <div className="main main-guide">
          <Guidecontent guideid={guideid} search={search} />
        </div>
      </div>
    </>
  );
}
