import React from "react";
import { Container, Col, Row } from "reactstrap";

import contributorsList from "../../assets/contributors/contributorsList.json";

export default function Contributors() {
  React.useEffect(() => {
    document.body.classList.toggle("ox-hide");
    // Specify how to clean up after this effect:
    return function cleanup() {
      document.body.classList.toggle("ox-hide");
    };
  }, []);
  return (
    <>
      <div className="wrapper index-page index-style">
        <div className="squares square1" />
        <div className="squares square2" />
        <div className="squares square3" />
        <div className="squares square4" />
        <div className="squares square5" />
        <div className="squares square6" />
        <div className="squares square7" />
        <div className="main">
          <Container>
            <div className="space-50" />
            <div id="images">
              <h3 className="mb-5">List of Contributors</h3>
              <Row className="justify-content-center">
                {contributorsList.map((contributor, index) => (
                  <Col
                    className="mb-5 text-align-center"
                    lg="3"
                    md="5"
                    sm="6"
                    xs="8"
                    key={index}
                  >
                    <div className="contributor-tile">
                      <img
                        alt="..."
                        className="img-fluid rounded-circle shadow-lg width-150 height-150"
                        src={
                          require("assets/contributors/pfp/" + contributor.pfp)
                            .default
                        }
                      />
                      <h4 className="contributor-name mt-2 mb-0">{contributor.name}</h4>
                      <p className="contributor-discord">{contributor.discord}</p>
                      <hr className="contributor-divider" />
                      <div className="contributor-details d-flex flex-column">
                        <h4 className="mb-1">{contributor.role}</h4>
                        <small>"{contributor.desc}"</small>
                        <br />
                        {!contributor.kofi ? (
                          ""
                        ) : (
                          <a
                            className="kofi position-absolute"
                            href={contributor.kofi}
                            rel="noopener noreferrer"
                            target="_blank"
                          >
                            <small className="mb-0">
                              <i className="fa fa-coffee"></i> Buy me a coffee!
                            </small>
                          </a>
                        )}
                      </div>
                    </div>
                  </Col>
                ))}
              </Row>
            </div>
          </Container>
        </div>
      </div>
    </>
  );
}
